<template>
  <form
    @submit.prevent="handleSubmit()"
    class="relative flex h-full w-full flex-col bg-bg-neutral-default ipad:max-h-[90dvh]"
  >
    <!-- Header Start -->
    <div
      class="sticky top-spacing-0 z-[100] w-full bg-bg-neutral-default px-spacing-200 pb-spacing-200 pt-spacing-300 ipad:px-spacing-300 ipad:pt-spacing-400"
    >
      <!-- Back Button -->
      <div
        v-if="showBackButton"
        class="absolute left-spacing-200 top-spacing-300 cursor-pointer ipad:left-spacing-300"
        @click="handleBack()"
      >
        <IconArrowLeft
          class="h-spacing-300 w-spacing-300 text-content-on-neutral-secondary hover:text-content-on-neutral-primary"
        />
      </div>

      <!-- Heading -->
      <h2
        v-if="heading"
        class="m-auto max-w-[85%] text-center text-overline/l/relaxed/500 text-content-on-neutral-secondary ipad:text-overline/xl/relaxed/500"
      >
        {{ heading?.toUpperCase() }}
        <p
          v-if="subHeading"
          class="text-body/m/relaxed/400 text-content-on-neutral-tertiary"
        >
          {{ subHeading }}
        </p>
      </h2>

      <!-- Close Button -->
      <div
        v-if="showCloseButton"
        class="absolute right-spacing-200 top-spacing-300 cursor-pointer ipad:right-spacing-300"
        @click="handleClose()"
      >
        <IconX
          class="h-spacing-300 w-spacing-300 text-content-on-neutral-secondary hover:text-content-on-neutral-primary"
        />
      </div>
    </div>
    <!-- Header End -->

    <!-- Form Body -->
    <div
      class="relative flex-1 overflow-y-auto px-spacing-200 py-spacing-50 ipad:px-spacing-300"
    >
      <slot name="form-body" />
    </div>

    <!-- Footer Start -->
    <div
      v-if="showSubmitButton && !showCustomButtons"
      class="sticky bottom-0 z-[100] flex w-full items-center gap-spacing-200 bg-bg-neutral-default px-spacing-200 pb-spacing-200 pt-spacing-200 ipad:px-spacing-300"
    >
      <div v-if="formType === 'delete'" @click="handleClose()" class="flex-1">
        <GeneralButton
          :button-type="'button'"
          :type="'neutral'"
          :size="'large'"
          :emphasis="'primary'"
          :icon-position="'none'"
          :label="'Cancel'"
          :is-loading="false"
          :is-disabled="false"
        />
      </div>
      <div @click="handleSubmit()" class="flex-1">
        <GeneralButton
          :button-type="'button'"
          :type="submitButtonType"
          :size="'large'"
          :emphasis="'primary'"
          :icon-position="'none'"
          :label="submitButtonLabel"
          :is-loading="isFormSubmitting"
          :is-disabled="submitButtonDisabled"
        />
      </div>
    </div>
    <div
      v-else-if="showCustomButtons && customButtons?.length > 0"
      class="sticky bottom-0 z-[100] flex w-full items-center gap-spacing-200 bg-bg-neutral-default px-spacing-200 pb-spacing-200 pt-spacing-200 ipad:px-spacing-300"
    >
      <GeneralButton
        v-for="(button, index) in customButtons"
        :key="index"
        :button-type="'button'"
        :type="button?.type"
        :size="'large'"
        :emphasis="button?.emphasis"
        :icon-position="'none'"
        :label="button?.label"
        :is-loading="isFormSubmitting"
        :is-disabled="button?.isDisabled"
        @click.native="handleSubmit(button?.payload)"
        class="flex-1"
      />
    </div>
    <!-- Footer End -->
  </form>
</template>

<script>
export default {
  props: {
    heading: {
      type: String,
      required: false,
    },
    subHeading: {
      type: String,
      required: false,
    },
    showCloseButton: {
      type: Boolean,
      default: true,
    },
    showBackButton: {
      type: Boolean,
      default: false,
    },
    showSubmitButton: {
      type: Boolean,
      default: true,
    },
    submitButtonLabel: {
      type: String,
      default: "Add",
    },
    submitButtonType: {
      type: String,
      default: "success",
    },
    submitButtonDisabled: {
      type: Boolean,
      default: false,
      required: false,
    },
    isFormSubmitting: {
      type: Boolean,
      default: false,
    },
    formType: {
      type: String,
      default: "input",
    },
    customButtons: {
      type: Array,
      default: () => [],
    },
    showCustomButtons: {
      type: Boolean,
      default: false,
    },
  },
  components: {
    IconX: () => import("@/components/svgIcons/x.vue"),
    IconArrowLeft: () => import("@/components/svgIcons/arrow-left.vue"),
    GeneralButton: () => import("@/components/General/CustomButton.vue"),
  },
  methods: {
    handleSubmit(payload) {
      if (payload) {
        this.$emit("form-submitted", payload);
      } else {
        this.$emit("form-submitted");
      }
    },
    handleBack() {
      this.$emit("back-pressed");
    },
    handleClose() {
      this.$emit("close-pressed");
    },
  },
};
</script>
